:root {
  --teal-accent: #1ea1b0;
  --red-accent: #ad2828;
  --gradient-body1: #122325;
  --gradient-body2: #231616;
  --blue3: #124e6d;
  --gradient-body2: #6d1212;
  --text-header-blue: #14eff7;
  --text-header-red: #f62e2e;
  --bg-1: #0f1111;
  --gradient-body2: #110d0e;
  --promo-claim-button: #e4c413;
  --promo-claim-text: #3a2f0d;
  --paragraphs: 'Abel', sans-serif;
  --header1: 'Oswald', sans-serif;
  --header2: 'Roboto', sans-serif;
}

.gradientBackground {
  background: rgb(18, 35, 37);
  background: linear-gradient(
    0deg,
    rgba(18, 35, 37, 1) 0%,
    rgba(35, 22, 22, 1) 100%
  );
}
.gradientBorder {
  background: rgb(30, 161, 176);
  background: linear-gradient(
    0deg,
    rgba(30, 161, 176, 1) 0%,
    rgba(173, 40, 40, 1) 100%
  );
}

.filterShadow {
  filter: blur(6px);
  /* position: absolute; */

  z-index: 1;
  background: linear-gradient(
      45deg,
      rgba(30, 161, 176, 1) 0%,
      rgba(173, 40, 40, 1) 100%
    )
    0% 0% / 300% 300%;
  animation: 2s linear 0s infinite normal none running glow;
  border-radius: 16px;
}

@keyframes glow {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

html {
  background: var(--bg-1);
}
html,
body {

  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}
@media screen and (max-width: 1070px) {
  html {
    font-size: 80%;
  }
}
@media screen and (max-width: 830px) {
  html {
    font-size: 78%;
  }
}
@media screen and (max-width: 593px) {
  html {
    font-size: 73%;
  }
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

#WEB3_CONNECT_MODAL_ID {
  position: relative;
  z-index: 10;
}

.viewportContainer {
  grid-column: 2 / span 1;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  padding: 2px;
  border-radius: 6px;
}

.leftNavContainer {
  grid-column: 1 / span 1;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  padding: 2px 2px;
}

.hamburgerMenu {
  display: none;
}
@media screen and (max-width: 576px) {
  .viewportContainer {
    grid-column: 1 / span 2;
  }
  .leftNavContainer {
    grid-column: none;
    position: absolute;
    z-index: 10;
    height: 100%;
    width: 30%;
    display: flex;
z-index: 10;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    padding: 2px 2px;
  }
  .hamburgerMenu {
    display: block;
    position: absolute;
    top: 5px;
    left: 5px;
    width: 20px;
    color: white;
    height: 20px;
  }
}

