.dapp-container {
  height: 100%;
  width: 100%;

  justify-content: center;
}

.dapp-top {
  height: 70%;
  width: 85%;
  display: grid;
  margin: auto;
  grid-template-columns: 50% 50%;
  grid-template-rows: 100%;
}
.dapp-bottom {
  height: 30%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
}

.promo-token-border {
  height: 90%;
  width: 95%;
  padding: 1px;
}
.promo-token-modal {
  height: 100%;
  width: 100%;
  background-image: url('../assets/bg-promotion.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 100%;
}
.promo-logo-container {
  height: 100%;
}
.promo-logo {
  height: 65%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.promo-claim-button-container {
  height: 35%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}



.promo-socials {
  display: flex;
  justify-content: center;
  align-items: center;
}
.promo-socials i {
  color: white;
}
.dapp-summary {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 40% 30% 30%;
}

@media screen and (max-width: 576px) {
  
  .dapp-summary {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 16% 42% 42%;
  }
}
.dapp-summary > span {
  align-self: center;
  justify-self: center;
  color: white;
  text-transform: capitalize;
  font-weight: 500;
  font-size: 2rem;
  font-family: var(--header1);
}

.dapp-summary-icon-container {
  display: flex;
  justify-content: space-around;
}
.dapp-summary-data {
  width: 60%;
  display: flex;
  flex-direction: column;
}
@media screen and (max-width: 576px) {
  .dapp-summary-data {
    text-align: center;
  }
  .dapp-summary-icon-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.dapp-summary-data > span {
  font-weight: 600;
  font-size: 1.2rem;
}
.dapp-summary-data > span:nth-child(2) {
  font-family: var(--header2);
}
.dapp-summary-icon-ee {
  display: inline-block;
  width: 68px;
  height: 68px;
  background: url(../assets/token-logo.png);
  background-position: 50% 50%;
  background-size: cover;
  border-radius: 50%;
}
@media screen and (max-width: 576px) {
  .summary-divider {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    margin: 0;
    padding: 0;
  }

  .promo-token-modal {
    height: 100%;
    width: 100%;
    z-index: 3;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: 100%;
  }
  .dapp-summary-icon-ee {
    width: 45px;
    height: 45px;
  }
}
.dapp-summary-icon-bnb {
  display: inline-block;
  width: 75px;
  height: 75px;
  background: url(../assets/token-rewards.png);
  background-position: 50% 50%;
  background-size: cover;
  border-radius: 50%;
}
div[class^="dapp-claim-button"] {
  height: 50%;
  font-weight: 500;
  display: flex;
  align-items: center;
  padding-left: 10px;
  width: 80%;
  font-size: 1.35rem;
  justify-self: center;
  cursor: pointer;
  color: white;
}
div[class^="dapp-claim-button"]:hover {
  transform: scale(1.06);
}
div[class^="dapp-claim-button"]:active {
  transform: scale(1);
}
.dapp-claim {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 40% 20% 20% 20%;
  font-family: var(--paragraphs);
}
.dapp-claim > span{

  font-family: var(--header1);
}
.promo-claim-button-container div.dapp-claim-button-promo {
  background: var(--promo-claim-button);
  font-family: var(--header1);

  justify-content: center;
  padding-left: 0;
  font-size: 0.9rem;
  border-radius: 5px;
  width: 65%;
}

.promo-right-side {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-size: 1.21rem;
  width: 98%;
}
.promo-right-side span:nth-child(1) {
  font-size: 1.1rem;
  font-family: var(--paragraphs);

}
/* token name */
.promo-right-side span:nth-child(2) { 
  font-size: 2rem;
  line-height: 0.9;
  text-transform: uppercase;
  color: var(--promo-claim-button);
  font-weight: 900;
  font-family: var(--header1);
}
.promo-right-side span:nth-child(3) {
  font-size: 1.4rem;
  font-family: var(--paragraphs);
  text-decoration: underline;
}
.promo-right-side span:nth-child(4) {
  font-size: 1rem;
}
@media screen and (max-width: 320px) {
  .promo-claim-button-container div.dapp-claim-button-promo {
    font-size: 0.8rem;
  }
}
.inner-promo-logo {
  height: 95%;
  width: 65%;
  /* background: gray; */
  border-radius: 5px;
  background-image: url('../assets/rugseekers.jpg');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
/*  
  phones
*/
@media screen and (max-width: 576px) {
  .promo-right-side span:nth-child(2) { 
    line-height: 1;
  }
  
  .promo-logo-container {

  }
  
  .promo-claim-button-container {

  }
 
  .promo-logo {
    height: 65%;
    display: flex;
    justify-content: center;
    align-items: center;
  }


  .dapp-claim {
    grid-template-rows: 18% 27% 27% 27%;
  }

  .dapp-top {
    height: 70%;
    width: 85%;
    display: grid;
    margin: auto;
    grid-template-rows: 50% 50%;
    grid-template-columns: 100%;
  }

  .dapp-summary-icon-bnb {
    width: 50px;
    height: 50px;
  }

  div[class^="dapp-claim-button"] {
    height: 85%;
    justify-self: center;
    align-self: center;
  }

  div.promo-claim-button-container {
    height: 35%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.dapp-claim > span {
  align-self: center;
  justify-self: center;
  color: white;
  text-transform: capitalize;
  font-weight: 500;
  font-size: 2rem;
}

