.container {
  height: 100vh;
  /* width: 100vw; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.fixed-address {
  position: fixed;
  width: 100vw;
  height: 4vh;
  bottom: 0;
  background: var(--red-accent);
  z-index: 20;
  color: white;
}
.fixed-address div {
  font-weight: 900;
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  cursor: pointer;
  margin: 0 0.5rem;
}
.fixed-address i {
  margin: 0 0.5rem;
  font-size: 1.3rem;
}
.fixed-address i.fa-times-circle {
  
}
.fixed-address span {
  padding: 0 0 0 0.5rem;
  font-weight: 900;
  font-size: 0.8rem;
}
.siteVertical {
  height: 100%;
  width: 90%;
  /* overflow-y: auto; */
}
.siteVertical::-webkit-scrollbar {
  height: 100%;
  width: 90%;
}
.siteVertical::-webkit-scrollbar-track {
  height: 100%;
  width: 90%;
}
.siteVertical::-webkit-search-results-button {
  background-color: violet;
}

.logoTop {
  height: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.logoTopImage {
  height: 100%;
  top: 0;
}

.siteContent {
  height: 60%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.mainModal {
  height: 95%;
  width: 95%;
  display: grid;
  position: relative;
  gap: 2%;
  grid-template-columns: 24% 74%;
  grid-template-rows: 100%;
  border-radius: 6px;
}
.leftNavBackground {
  position: absolute;
  grid-column: 1 / span 1;
  height: 95%;
  width: 23%;
  left: 2%;
  /* transform: scale(1.06, 1.02); */
  /* transform: scale(1, 1.02); */
}
.rightViewportBackground {
  position: absolute;
  grid-column: 1 / span 1;
  height: 95%;
  width: 71%;

  right: 2%;
}
.navImageContainer {
  position: absolute;
  display: inline-block;
  width: 75px;
  height: 75px;
  background: url(./assets/token-logo.png);
  background-position: 50% 50%;
  background-size: cover;
  border-radius: 50%;
  top: 0;
  transform: translate(0, -50%);
  z-index: 5;
}

.leftNavModal {
  height: 100%;
  width: 100%;
  border-radius: 6px;
  display: grid;
  z-index: 2;
  padding-top: 20%;
  box-shadow: inset 0 0 20px -13px rgba(255, 255, 255, 0.63);
  grid-template-rows: 50% 50%;
}
.navItemsContainer {
  display: grid;
  height: 100%;
  padding-left: 13%;
  grid-auto-rows: 1fr;
}

.navItem {
  height: 100%;
  width: 100%;
  color: white;
  font-size: 1rem;
  display: grid;
  grid-template-columns: 25% 75%;
  grid-template-rows: 100%;
  align-self: center;
  justify-self: center;
  overflow: hidden;
  justify-content: center;
  cursor: pointer;
  text-align: center;
}
.navItem svg {
  height: 50%;
  width: 50%;
}
.navItem:hover {
  color: turquoise;
}

.navIconContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 0.1rem; */
}
.navIcon {
  height: 100%;
  width: 75%;
  min-width: 30%;
  min-height: 100%;
  max-height: 50px;
  max-width: 50px;
  align-self: center;
  justify-self: center;
}
.navLabel {
  justify-self: flex-start;
  margin-left: 10px;
  align-self: center;
}



.dapp-claim-button-cmp {
  border-radius: 6px;
  background: var(--blue3);
  background-image: url("./assets/button-overlay-compound.png");
  background-position: 105% 55%;
  background-size: 25%;
  background-repeat: no-repeat;
}
.dapp-claim-button-claim {
  border-radius: 6px;
  background: var(--blue3);
  background-image: url("./assets/button-overlay-bsc.png");
  background-position: 102% 55%;
  background-size: 25%;
  background-repeat: no-repeat;
}
.dapp-claim-button-claim-any {
  border-radius: 6px;
  background: rgb(199, 48, 48);
  background-image: url("./assets/button-overlay-tokens.png");
  background-position: 105% 40%;
  background-size: 30%;
  background-repeat: no-repeat;
}

.leftNavBottom {
  overflow: hidden;
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.leftNavBottom > span {
  overflow: hidden;
  color: white;
  font-weight: 500;
  width: auto;
  text-overflow: ellipsis;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  margin-bottom: 5px;
  background-color: var(--text-header-red);
  text-align: center;
}
.leftNavBottom > span {
  padding: 10px 20px;
  border: 0px;
  border-radius: 4px;
  border: 1px solid;
  border-image-slice: 1;
  border-image-source: linear-gradient(to left, #3abed5, #d53a3a);
  background: linear-gradient(90deg, rgb(48, 33, 33) 0%, rgb(32, 39, 39) 100%);
  color: #fff;
  transition: ease-out 0.3s;
}

.leftNavBottom > span:hover {
  cursor: pointer;
  box-shadow: inset 300px 0 0 0 #e72e2e;
}

.leftNavBottom button {
  color: white;
  font-weight: 500;
  background-color: var(--text-header-red);
  border: none;
  cursor: pointer;
  border-radius: 5px;
  width: auto;
}
.whitepap-page {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.whitepap-page a {
  font-weight: 900;
  font-size: 2.69rem;
  color: white;
  text-decoration: underline;
}

.viewport {
  height: 100%;
  width: 100%;
  border-radius: 6px;
  overflow-y: auto;
  /* overflow-x: hidden; */
}
.viewport .connect-wallet {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
  padding: 0 2rem;
}

.bottomBanner {
  height: 20%;
  display: grid;
  /* grid-template-columns: 25% 25% 25% 25%; */

  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
  grid-auto-rows: minmax(140px, 140px);
}

.bottomCardContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: var(--header2);
  font-weight: 700;
  font-size: 1.3rem;
}
.cardTitle {
  color: aqua;
  font-weight: 700;
}
.bottomCardBorder {
  height: 95%;
  width: 81%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1px;
  border-radius: 5px;
}
.bottomCard {
  height: 100%;
  width: 100%;
  display: flex;
  border-radius: 5px;
  justify-content: center;
}

.timerCard {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 500;
}

.supply-card {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: white;
  font-weight: 700;
}

.burn-card {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: white;
  font-weight: 700;
}

.rewards-card {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: white;
  font-weight: 700;
}

.home-tab {
  background-image: url("./assets/homepage-panel-bg.png");
  background-attachment: fixed;
  background-position: center;
  align-items: center;
  flex-direction: column;
  /* background-position: ; */
  background-size: cover;

  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 50% 50%;
  color: white;
  padding: 0 1.5rem;
}
.home-tab {
  /* background-image: url('./assets/homepage-panel-bg.png'); */
  /* background-attachment: fixed; */
  background-position: center;
  /* background-position: ; */
  background-size: cover;

  height: auto;
  min-height: 100%;
  background-repeat: no-repeat;

  width: 100%;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 50% 50%;
  color: white;
  padding: 0 1.5rem;
  word-break: break-all;
}
.home-tab p {
  font-size: 1.9rem;
  font-weight: 900;
}

.home-top {
  /* width: 75%; */
  align-self: flex-start;
  display: flex;
  flex-direction: column;
  font-size: 1.1rem;
  font-weight: 500;
}

.home-bottom {
  /* width: 75%; */
  font-size: 1.1rem;
  font-weight: 500;
  /* align-self: center; */
  /* justify-self: flex-end; */
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 576px) {
  .container {
    height: auto;
  }

  .fixed-address div {
    justify-content: space-around;
  }
  .fixed-address span {
    width: min-content;
  }

  .viewportContainer {
    height: 600px;
  }
  .logoTop {
    height: 150px;
    margin: 10px 0;
  }
  .home-top {
    width: 90%;
  }
  .home-bottom {
    /* width: 90%; */
  }
  .leftNavContainer {
    position: fixed;
    height: 100vh;
    width: 40vw;
    border-radius: 0;
    left: 0;
    top: 0;
  }
  .leftNavModal {
    font-size: 1rem;
    font-family: var(--header1);
  }
  .navItem {
    font-size: 1.4rem;
  }
  .navItemsContainer {
    padding-left: 5%;
  }
  .navImageContainer {
    width: 40px;
    height: 40px;
    display: none;
  }
  .siteVertical {
    height: auto;
  }
  .bottomBanner {
    height: auto;
    margin-bottom: 5vh;
  }
}

.chart-container {
  text-decoration: underline;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  font-size: large;
  font-weight: 900;
  color: var(--text-header-red);
}
.chart-container iframe {
  height: 100%;
  width: 100%;
}
